  text-align:
}

#main{
  padding: 0px;
  box-sizing: border-box;
  width: 60%;
  height: 100%;
}

#book-list{
  padding: 0;
  height: 26em;
  overflow: scroll;
}

#book-list li{
  display: inline-block;
  margin: 12px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #880E4F;
  box-shadow:  1px 2px 3px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  color: #880E4F;
}


form{
  background: #fff;
  padding: 20px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 400px;
}

form .field{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

form label{
  text-align: right;
  padding: 6px;
}

form select, form input{
  margin: 4px;
  padding: 6px;
  box-sizing: border-box;
}

form button{
  color: #fff;
  font-size: 2em;
  background: #AD1457;
  border: 0;
  padding: 1px 10px;
  border-radius: 60%;
  cursor: pointer;
  position: absolute;bottom: 10px;
  left: 10px;
  focus , active: {
    outline:none;
  }
}
button:active, a:focus {
  outline: none;
}

#book-details {
  position: fixed;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
  background: #AD1457;
  padding: 30px;
  overflow: auto;
  box-shadow:  -2px  -3px 5px (0,0,0,0.3);
  box-sizing: border-box;
  color: #fff;
}

.error{
  border: 1px solid red;
}
